<template>
  <img alt="Vue logo" src="./assets/logo.png" />
  <ListPage v-if="isList" @detail="onClickDetail" />
  <DetailPage v-else :detail="detail" @list="onClickList" />
</template>

<script>
import ListPage from "./components/ListPage.vue";
import DetailPage from "./components/DetailPage.vue";

export default {
  name: "App",
  components: {
    ListPage,
    DetailPage,
  },
  data() {
    return {
      isList: true,
      detail: {},
    };
  },
  created() {},
  watch: {},
  methods: {
    onClickDetail(payload) {
      this.detail = payload;
      this.isList = false;
    },
    onClickList() {
      this.isList = true;
    },
  },
};
</script>

<style>
#app {
  font-family: Avenir, Helvetica, Arial, sans-serif;
  -webkit-font-smoothing: antialiased;
