<template>
  <div>
    <h1>음악 차트</h1>
    <p>{{ date }}</p>
  </div>
  <div>
    <v-btn :style="`${type === 'a' ? 'COLOR: red;' : ''}`" @click="type = 'a'"
      >국내</v-btn
    >
    <v-btn :style="`${type === 'b' ? 'COLOR: red;' : ''}`" @click="type = 'b'"
      >해외</v-btn
    >
  </div>
  <ul>
    <li v-for="(item, index) in list" :key="index">
      <v-btn @click="onClickDetail(item)">
        <span>[{{ index }}]</span> {{ item.title }}
      </v-btn>
      <p>
        {{ item.singer }}
      </p>
    </li>
  </ul>
</template>

<script>
// import axios from "axios";
import dayjs from "dayjs";

export default {
  name: "App",
  components: {
    // HelloWorld,
  },
  data() {
    return {
      version: "",
      date: dayjs().format("YYYY년 MM월 DD일 HH:mm"),
      list: [],
      type: "a",
    };
  },
  created() {
    this.fetchData(this.type);
  },
  watch: {
    type(value) {
      this.fetchData(value);
    },
  },
  methods: {
    onClickDetail(param) {
      this.$emit("detail", param);
    },
    async fetchData(param) {
      if (param === "a") {
        this.list = [
          {
            title: "제목1",
            singer: "가수1",
          },
          {
            title: "제목2",
            singer: "가수2",
          },
          {
            title: "제목3",
            singer: "가수3",
          },
          {
            title: "제목4",
            singer: "가수4",
          },
        ];
      } else {
        this.list = [
          {
            title: "title1",
            singer: "singer1",
          },
          {
            title: "title2",
            singer: "singer2",
          },
          {
            title: "title3",
            singer: "singer3",
          },
          {
            title: "title4",
            singer: "singer4",
          },
        ];
      }
      // try {
      //   const { data } = await axios.get("/version");
      //   this.version = data;
      // } catch (error) {
      //   this.version = "axios call error";
      // }
    },
  },
};
</script>

<style>
#app {
  font-family: Avenir, Helvetica, Arial, sans-serif;
  -webkit-font-smoothing: antialiased;
  -moz-osx-font-smoothing: grayscale;
  text-align: center;
  color: #2c3e50;
  margin-top: 60px;
}
</style>
